<template>
<div style="width: 100%" class="center">
  <van-nav-bar
      left-text=""
      left-arrow

      title=""
      @click-left="onClickLeft"
  />
  <van-divider></van-divider>
  <div class="qrcode" ref="qrCodeUrl"></div>
  <van-divider></van-divider>
  <van-form disabled>
    <van-field
        v-model="userInfo.tname"
        name=""
        label=""
    />
    <van-field
        label=""
        v-model="computeSex"
    >
    </van-field>
    <van-field
        v-model="userInfo.idcardnum"
        name=""
        label=""
    /><van-field
      v-model="userInfo.phonenum"
      name=""
      label=""
  />
    <van-field
        clickable
        name="area"
        :value="userInfo.area"
        label=""
        placeholder=""
    />
    <van-field
        v-model="userInfo.address"
        name=""
        label=""
    />
  </van-form>
  <div style="margin: 16px;">
  <van-button round block type="info" :to="'edit?idcardnum='+this.userInfo.idcardnum"></van-button>
  </div>
  <div style="margin: 16px;">
    <van-button round block type="danger" @click="handleDelete"></van-button>
  </div>
</div>
</template>

<script>
import QRCode from 'qrcodejs2'
import {getUrlParam} from "@/components/js/GetUrlParam";
import {Dialog, Toast} from "vant";

export default {
  name: "infoView",
  data(){
    return{
      userInfo:{
        address:'',
        sex:1,
        phonenum:'',
        idcardnum:'',
        tname:'',
        area: '',
      },
        showArea: false,
        areaList: {},
    }
  },
  computed:{
    computeSex(){
      return this.userInfo.sex===1?'':''
    }
  },
  created() {
      this.userInfo.idcardnum=getUrlParam("idcardnum")
      if (this.userInfo.idcardnum===''||this.userInfo.idcardnum===null){
        this.$router.push("/user/info")
      }else{
        this.checkLogin();
      }
  },
  methods: {
    checkLogin(){
      this.axios.post(
          '/wechat/checklogin',
      ).then((response) =>{
        if (response.data.flag){
          //
          this.getUserInfo()
        }else {
          Toast.fail(response.data.msg);
          this.$router.push("/user/info")
        }
      })
          .catch(function (error) {
            console.log(error);
          });
    },
    handleDelete(){
      Dialog.confirm({
        title: ' ',
        message: '',
      })
          .then(() => {
            Toast.loading({
              message: '...',
              forbidClick: true,
            });
            this.axios.post(
                '/wechat/delWechatUser',
                this.qs.stringify({
                  idcardnum:this.userInfo.idcardnum,
                })
            ).then((response) =>{
              if (response.data.flag){
                Toast.success(response.data.msg);
                this.$router.push("/user/info")
              }else {
                Toast.fail(response.data.msg);
                if (response.data.code===408){
                  this.$router.push("/user/info")
                }
              }
            })
                .catch(function (error) {
                  console.log(error);
                });
          })
          .catch(() => {
            // on cancel
          });
    },
    creatQrCode() {
      new QRCode(this.$refs.qrCodeUrl, {
        text: '{"idcardnum":"'+this.userInfo.idcardnum+'","tname":"'+this.userInfo.tname+'"}', // 
        width: 180,
        height: 180,
        colorDark: '#000000',
        colorLight: '#ffffff',
        correctLevel: QRCode.CorrectLevel.H
      })
    },
    getUserInfo(){
      this.axios.post(
          '/wechat/userInfoViewAndEdit',
          this.qs.stringify({
            idcardnum: this.userInfo.idcardnum
          })
      ).then((response) =>{
        if (response.data.flag){
          this.userInfo=response.data.data[0];
          this.creatQrCode()
        }else {
          Toast.fail(response.data.msg);
          if (response.data.code===408){
            this.$router.push("/user/info")
          }
        }
      })
          .catch(function (error) {
            console.log(error);
          });
    },
    onClickLeft() {
      this.$router.push("/user/info")
    },
  },
}
</script>

<style scoped>
.qrcode{
  width: 180px;
  height: 180px;
  margin: 0 auto;
}
</style>
